import React from "react";
import s from "./Users.module.css";
import userPhoto from "../../assets/images/user-female-circle.png";
import {NavLink} from "react-router-dom";




let User = (props) => {
    return (<div key={props.u.id} className={s.item}>
        <div className={s.flex_wrapper}>
            <div className={s.avatar_wrapper}>
                <div className={s.avatar}>
                    <NavLink to={'/profile/'+ props.u.id}>
                        <img src={props.u.photos.small != null ? props.u.photos.small : userPhoto}/>
                    </NavLink>
                </div>
                <div className={s.follow_btn}>
                    {!props.u.followed
                        ? <button disabled={props.followingInProgress.some(id => id === props.u.id)} onClick={() => {props.follow(props.u.id)}}>Follow</button>
                        : <button disabled={props.followingInProgress.some(id => id === props.u.id)} onClick={() => {props.unfollow(props.u.id)}}>Unfollow</button>}
                </div>
            </div>
            <div className={s.info_wrapper}>
                <div className={s.fullName}>{props.u.name}</div>
                <div className={s.status}>{props.u.status}</div>
            </div>
        </div>
        <div className={s.location_wrapper}>
            <div className={s.country}>Украина</div>
            <div className={s.city}>Киев</div>
        </div>
    </div>)
}

export default User;
