import  React, {useState} from "react";
import s from "./Paginator.module.css";
import arrow from "../../../assets/images/arrow.png";


let Paginator = (props) => {

    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    let portionSize = 10;
    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let startPortion = (portionNumber - 1) * portionSize + 1;
    let finishPortion = portionNumber * portionSize;

    for (let i = 1;
         i <=
         pagesCount;
         i++) {
        pages.push(i);
    }

    return (

        <div className={s.pages_wrapper}>
            {portionNumber > 1  && <img className={`${s.left_arrow} ${s.arrow}`} src={arrow} onClick={()=>setPortionNumber(portionNumber-1)}/>}
            {pages.filter(p => p >=
                startPortion &&
                p <=
                finishPortion)
            .map(p => {
                    return (<span key={p} onClick={() => {
                        props.onPageChanged(p)
                    }} className={props.currentPage ===
                    p ? s.selectedPage : s.pagesItem}>{p}</span>)
                })}
            {portionCount > portionNumber && <img className={`${s.right_arrow} ${s.arrow}`} src={arrow} onClick={()=>setPortionNumber(portionNumber+1)}/>}
        </div>
    )


}
export default Paginator;
